.modal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  z-index: 1000;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  overflow-y: auto;
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  animation: fadeIn 250ms ease-in-out;
}
.modal .dialog {
  width: 100%;
  max-width: 960px;
  min-height: 100px;
  border-radius: 4px;
  position: relative;
  border: none;
  color: black;
  font-weight: 300;
}
.modal .dialog .dialog-close {
  position: absolute;
  top: 0;
  right: 0;
  width: 3em;
  height: 3em;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  min-width: 3em;
  box-shadow: none;
}
.modal .dialog .dialog-close::before,
.modal .dialog .dialog-close::after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  right: 5px;
  left: 5px;
  border-bottom: 1px solid black;
  transform: rotate(45deg);
}
.modal .dialog .dialog-close::after {
  transform: rotate(-45deg);
}
.modal .dialog .dialog-close:focus::before,
.modal .dialog .dialog-close:hover::before,
.modal .dialog .dialog-close:focus::after,
.modal .dialog .dialog-close:hover::after {
  border-color: cornflowerblue;
}
.modal .dialog .dialog-content {
  padding: 1em;
  background-color: white;
  border: none;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
